$search-bar-border: #e6e6e6;
$stats-color: #70757a;
$clear-button-color: #d9d9d9;
$background-color: #fff;
$font-color: #000;
$logo-blue: #4885ed;
$logo-red: #db3236;
$logo-yellow: #ffc107;
$logo-green: #3cba54;
$link-color: #1a0dab;
$link-visited-color: #609;
$link-outline-color: #000;
$font-stack: 'Roboto', Arial, sans-serif;

%flex {
  display: flex;
  align-items: center;
}

@mixin flexColumn {
  @extend %flex;
  flex-direction: column;
  justify-content: flex-start;
}

@mixin flexCenter {
  @extend %flex;
  justify-content: center;
}

@mixin mq($size) {
  @media only screen and (min-width: $size) {
    @content;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  background-color: $background-color;
  color: $font-color;
  width: 100vw;
  min-height: 100vh;
  font-family: 'Roboto';
  font-size: 22px;
}
main {
  @include flexColumn;
  min-height: calc(100vh - 60px);
}
footer {
  width: 100%;
  height: 50px;
  @include flexCenter;
}

p {
  color: $font-color;
  font-size: 0.5 rem;
  @include mq(760px) {
    font-size: 1rem;
  }
}
a {
  color: $link-visited-color;
}

img {
  display: block;
}
